<template>
  <div class="box">
    <div class="FormData">
      <el-card class="searchModule">
        <el-form :model="box" label-width="100px" :inline="true" :ref="box">
          <el-form-item label prop="queryValue">
            <el-input v-model="box.queryValue" clearable></el-input>
          </el-form-item>

          <el-button type="primary" @click="search()"></el-button>
          <el-button type="primary" @click="resetForm(box)"></el-button>
        </el-form>
      </el-card>
    </div>

    <div class="contentB">
      <el-card>
        <div class="clickBtn">
          <el-button class="operateBtn" type="primary" @click="addData"></el-button>
          <el-button type="primary" @click="delData()"></el-button>
        </div>

        <div class="tableData">
          <sirui-table
            class="operateTable"
            :page="page"
            :loading="load"
            :data="tableData"
            @handleSelectionChange="handleSelectionChange"
            @handleSizeChange="handleSizeChange"
            @handleCurrentChange="handleCurrentChange"
            :tableHeight="tableHeight"
          >
            <el-table-column align="center" type="selection" width="45"></el-table-column>
            <el-table-column
              v-for="(item,index) in columbList"
              :key="index"
              :prop="item.prop"
              :label="item.label"
              :width="item.width"
              :sortable="item.sort"
              :fixed="item.fixed"
              align="center"
              show-overflow-tooltip
            ></el-table-column>
          </sirui-table>
        </div>
      </el-card>
    </div>
    <BoxAdd ref="addData" v-on:search="search"></BoxAdd>
  </div>
</template>

<script>
import SiruiTable from "@/components/SiruiTable";
import BoxAdd from "@/boxManages/BoxAdd";

export default {
  name: "BoxManages",
  components: {
    SiruiTable,
    BoxAdd
  },
  data() {
    return {
      box: {
        queryValue: ""
      },

      tableHeight: 400,
      dialogVisible: false,
      page: {
        pages: 0, //
        pageNum: 1, //
        pageSize: 10, //
        total: 0 //
      },
      load: false,
      multipleSelection: [],
      columbList: [
        {
          prop: "boxStandard",
          label: "",
          width: 200
        },
        {
          prop: "totalNumber",
          label: ""
        },
        {
          prop: "usedNumber",
          label: ""
        },
        {
          prop: "inventoryNumber",
          label: ""
        },
        {
          prop: "useRatio",
          label: ""
        },
        {
          prop: "boxUnitPrice",
          label: ""
        }
      ],
      tableData: [
        // {
        //   boxCode: "001",
        //   boxCount: "366",
        //   useCount: "34534",
        //   surplusCount: "21016",
        //   boxScale: "456754",
        //   memberPoints: "100"
        // },
        // {
        //   boxCode: "002",
        //   boxCount: "366",
        //   useCount: "34534",
        //   surplusCount: "21016",
        //   boxScale: "456754",
        //   memberPoints: "100"
        // },
        // {
        //   boxCode: "003",
        //   boxCount: "366",
        //   useCount: "34534",
        //   surplusCount: "21016",
        //   boxScale: "456754",
        //   memberPoints: "100"
        // },
        // {
        //   boxCode: "004",
        //   boxCount: "366",
        //   useCount: "34534",
        //   surplusCount: "21016",
        //   boxScale: "456754",
        //   memberPoints: "100"
        // },
        // {
        //   boxCode: "005",
        //   boxCount: "366",
        //   useCount: "34534",
        //   surplusCount: "21016",
        //   boxScale: "456754",
        //   memberPoints: "100"
        // }
      ]
    };
  },
  mounted() {
    this.getData();
  },
  methods: {
    // 
    search() {
      this.getData();
    },
    //
    getData() {
      this.$axios
        .get(
          "prod-api/backend/box/standard/list?" +
            "pageNum=" +
            this.page.pageNum +
            "&pageSize=" +
            this.page.pageSize +
            "&searchValue=" +
            this.box.queryValue
        )
        .then(res => {
          if (200 == res.data.code) {
            this.tableData = res.data.rows;
            this.page.total = res.data.total;
            this.$message({
              showClose: true,
              message: "",
              type: "success"
            });
          } else {
            this.$message({
              showClose: true,
              message: res.data.msg,
              type: "error"
            });
          }
        });
    },
    // 
    // resetfields
    // $ref
    resetForm(formName) {
      if (this.$refs[formName] !== undefined) {
        this.$refs[formName].resetFields();
      }
    },

    handleSelectionChange(row) {
      this.multipleSelection = row;
      //JSON.stringify
      // alert(JSON.stringify(this.multipleSelection));
    },
    // 
    handleSizeChange(val) {
      this.page.pageSize = val;
      this.getData();
    },
    // 
    handleCurrentChange(val) {
      this.page.pageNum = val;
      this.getData();
    },

    // 
    addData() {
      this.$refs.addData.openDialog(true);
    },

    //
    delData() {
      if (this.multipleSelection.length >= 1) {
        if (this.multipleSelection.length != 0) {
          this.$confirm(",?", "", {
            confirmButtonText: "",
            cancelButtonText: "",
            type: "warning"
          })
            .then(() => {
              //
              let params = {
                ids: []
              };
              for (var i = 0; i < this.multipleSelection.length; i++) {
                params.ids.push(this.multipleSelection[i].id);
              }
              params.ids = params.ids.join(",");
              this.$axios
                .delete("prod-api/backend/box/standard/delete/" + params.ids)
                .then(res => {
                  // this.loading.endLoading();
                  if (200 == res.data.code) {
                    // this.searchForm();
                    this.$message({
                      type: "success",
                      message: res.data.msg
                    });
                  } else {
                    this.$message({
                      showClose: true,
                      message: "",
                      type: "error"
                    });
                  }
                });
            })
            .catch();
        }
      }
    }
  }
};
</script>

<style lang="scss" scoped>
.formData {
  background: #fff;
}
.clickBtn {
  margin-top: 10px;
  padding: 5px;
}

.contentB {
  height: 100%;
  background: #fff;

  margin: 10px;
}
</style>



