<template>
  <div class="ad">
    <div class="FormData">
      <el-card class="searchModule">
        <el-form :model="ad" label-width="100px" :inline="true" :ref="ad">
          <el-form-item label prop="queryValue">
            <el-input v-model="ad.queryValue" clearable></el-input>
          </el-form-item>
          <el-button type="primary" @click="search()"></el-button>
          <el-button type="primary" @click="resetForm('ad')"></el-button>
        </el-form>
      </el-card>
    </div>

    <div class="contentB">
      <el-card>
        <div class="clickBtn">
          <el-button class="operateBtn" type="primary" @click="addData()"></el-button>
          <el-button type="primary" @click="editData()"></el-button>
          <el-button type="primary" @click="isOpen()"></el-button>
          <el-button type="primary" @click="isStop()"></el-button>
          <el-button type="primary" @click="delData()"></el-button>
        </div>

        <div class="tableData">
          <sirui-table
            class="operateTable"
            :page="page"
            :loading="load"
            :data="tableData"
            @handleSelectionChange="handleSelectionChange"
            @handleSizeChange="handleSizeChange"
            @handleCurrentChange="handleCurrentChange"
            :tableHeight="tableHeight"
          >
            <el-table-column align="center" type="selection" width="45"></el-table-column>
            <el-table-column
              v-for="item in columbList"
              :key="item.num"
              :prop="item.prop"
              :label="item.label"
              :width="item.width"
              :fixed="item.fixed"
              align="center"
              :show-overflow-tooltip="true"
            ></el-table-column>
            <el-table-column prop="imgUrl" label="" min-wigth="50px">
              <!--  -->
              <template slot-scope="scope">
                <img :src="scope.row.imgUrl" min-width="50px" height="50px" />
              </template>
            </el-table-column>
          </sirui-table>
        </div>
        <AdAdd ref="addData" v-on:search="search"></AdAdd>
        <AdEdit ref="editData" v-on:search="search"></AdEdit>
      </el-card>
    </div>
  </div>
</template>
  

<script>
import SiruiTable from "@/components/SiruiTable";
import AdAdd from "@/adManages/AdAdd";
import AdEdit from "@/adManages/AdEdit";

export default {
  name: "AdManages",
  components: {
    SiruiTable,
    AdAdd,
    AdEdit
  },
  data() {
    return {
      ad: {
        queryValue: ""
      },
      tableHeight: 400,
      dialogVisible: false,
      page: {
        pages: 0, //
        pageNum: 1, //
        pageSize: 10, //
        total: 0 //
      },
      load: false,
      multipleSelection: [],
      rules: {},
      columbList: [
        {
          prop: "id",
          label: "",
          width: 100,
          num: 1
        },
        {
          prop: "title",
          label: "",
          width: 100,
          num: 2
        },
        {
          prop: "content",
          label: "",
          width: 230,
          num: 3
        },
        {
          prop: "points",
          label: "",
          width: 100,
          num: 4
        },

        {
          prop: "sortNo",
          label: "",
          width: 100,
          num: 5
        },
        {
          prop: "isEnable",
          label: "",
          width: 100,
          num: 6
        },
        {
          prop: "delFlag",
          label: "",
          width: 100,
          num: 7
        }
      ],
      tableData: []
    };
  },
  mounted() {
    this.getData();
  },
  methods: {
    // handleClick(row) {
    //   alert(row);
    // },
    // 
    search() {
      this.getData();
    },
    //
    getData() {
      this.$axios
        .get(
          "prod-api/backend/advertisement/list?" +
            "pageNum=" +
            this.page.pageNum +
            "&pageSize=" +
            this.page.pageSize +
            "&searchValue=" +
            this.ad.queryValue
        )
        .then(res => {
          if (200 == res.data.code) {
            for (var i = 0; i < res.data.rows.length; i++) {
              if (res.data.rows[i].imgUrl) {
                res.data.rows[i].imgUrl = "prod-api/" + res.data.rows[i].imgUrl;
              }
            }
            this.tableData = res.data.rows;
            this.page.total = res.data.total;
            this.$message({
              showClose: true,
              message: "",
              type: "success"
            });
          } else {
            this.$message({
              showClose: true,
              message: res.data.msg,
              type: "error"
            });
          }
        });
    },
    // 
    // resetfields
    // $ref
    resetForm(formName) {
      if (this.$refs[formName] !== undefined) {
        this.$refs[formName].resetFields();
      }
    },

    handleSelectionChange(row) {
      this.multipleSelection = row;
      //JSON.stringify
      // alert(JSON.stringify(this.multipleSelection));
    },
    // 
    handleSizeChange(val) {
      this.page.pageSize = val;
    },
    // 
    handleCurrentChange(val) {
      this.page.pageNum = val;
    },

    // 
    addData() {
      this.$refs.addData.openDialog(true);
    },

    // 
    addData() {
      this.$refs.addData.openDialog(true);
    },
    //
    editData() {
      if (1 == this.multipleSelection.length) {
        //
        this.$refs.editData.openDialog(
          true,

          JSON.parse(JSON.stringify(this.multipleSelection[0]))
        );
      } else {
        this.$message({
          showClose: true,
          message: "",
          type: "error"
        });
      }
    },
    //
    isOpen() {
      if (this.multipleSelection.length >= 1) {
        if (0 != this.multipleSelection.length) {
          this.$confirm("", "", {
            confirmButtonText: "",
            cancelButtonText: "",
            type: "warning"
          })
            .then(() => {
              let param = {
                ids: []
              };
              for (var i = 0; i < this.multipleSelection.length; i++) {
                param.ids.push(this.multipleSelection[i].id);
              }
              param.ids = param.ids.join(",");
              this.$axios
                .put(
                  "prod-api/backend/advertisement/operate/enable/" + param.ids,
                  param
                )
                .then(res => {
                  if (200 == res.data.code) {
                    this.$message({
                      showClose: true,
                      message: res.data.msg,
                      type: "success"
                    });
                  } else {
                    this.$message({
                      showClose: true,
                      message: res.data.msg,
                      type: "error"
                    });
                  }
                });
            })
            .catch(() => {
              this.$message({
                type: "info",
                message: ""
              });
            });
        }
      } else {
        this.$message({
          showClose: true,
          message: "",
          type: "error"
        });
      }
    },

    //
    isStop() {
      if (this.multipleSelection.length >= 1) {
        if (0 != this.multipleSelection.length) {
          this.$confirm("", "", {
            confirmButtonText: "",
            cancelButtonText: "",
            type: "warning"
          })
            .then(() => {
              let param = {
                ids: []
              };
              for (var i = 0; i < this.multipleSelection.length; i++) {
                param.ids.push(this.multipleSelection[i].id);
              }
              param.ids = param.ids.join(",");
              this.$axios
                .put(
                  "prod-api/backend/advertisement/disable/" + param.ids,
                  param
                )
                .then(res => {
                  if (200 == res.data.code) {
                    this.$message({
                      showClose: true,
                      message: res.data.msg,
                      type: "success"
                    });
                  } else {
                    this.$message({
                      showClose: true,
                      message: res.data.msg,
                      type: "error"
                    });
                  }
                });
            })
            .catch(() => {
              this.$message({
                type: "info",
                message: ""
              });
            });
        }
      } else {
        this.$message({
          showClose: true,
          message: "",
          type: "error"
        });
      }
    },

    //
    delData() {
      if (this.multipleSelection.length >= 1) {
        if (this.multipleSelection.length != 0) {
          this.$confirm(",?", "", {
            confirmButtonText: "",
            cancelButtonText: "",
            type: "warning"
          })
            .then(() => {
              //
              let params = {
                ids: []
              };
              for (var i = 0; i < this.multipleSelection.length; i++) {
                params.ids.push(this.multipleSelection[i].id);
              }
              params.ids = params.ids.join(",");
              this.$axios
                .delete("prod-api/backend/advertisement/delete/" + params.ids)
                .then(res => {
                  // this.loading.endLoading();
                  if (200 == res.data.code) {
                    // this.searchForm();
                    this.$message({
                      type: "success",
                      message: res.data.msg
                    });
                  } else {
                    this.$message({
                      showClose: true,
                      message: "",
                      type: "error"
                    });
                  }
                });
            })
            .catch();
        }
      }
    }
  }
};
</script>

<style lang="scss" scoped>
.formData {
  background: #fff;
}
.clickBtn {
  margin-top: 10px;
  padding: 5px;
}

.contentB {
  height: 100%;
  background: #fff;

  margin: 10px;
}
</style>



