<template>
  <div class="view">
    <div class="FormData">
      <el-card class="searchModule">
        <el-form :model="view" label-width="150px" :inline="true" :ref="view">
          <el-form-item label prop="queryValue">
            <el-input v-model="view.queryValue" clearable></el-input>
          </el-form-item>
          <el-button type="primary" @click="search()"></el-button>
          <el-button type="primary" @click="resetForm(view)"></el-button>
        </el-form>
      </el-card>
    </div>

    <div class="contentB">
      <el-card>
        <div class="tableData">
          <sirui-table
            class="operateTable"
            :page="page"
            :loading="load"
            :data="tableData"
            @handleSelectionChange="handleSelectionChange"
            @handleSizeChange="handleSizeChange"
            @handleCurrentChange="handleCurrentChange"
            :tableHeight="tableHeight"
          >
            <el-table-column align="center" type="selection" width="45"></el-table-column>
            <el-table-column
              v-for="(item,index) in columbList"
              :key="index"
              :prop="item.prop"
              :label="item.label"
              :width="item.width"
              :sortable="item.sort"
              :fixed="item.fixed"
              align="center"
              show-overflow-tooltip
            ></el-table-column>
          </sirui-table>
        </div>
      </el-card>
    </div>
  </div>
</template>

<script>
import SiruiTable from "@/components/SiruiTable";

export default {
  name: "ViewManages",
  components: {
    SiruiTable
  },
  data() {
    return {
      view: {
        queryValue: ""
      },
      tableHeight: 400,
      dialogVisible: false,
      page: {
        pages: 0, //
        pageNum: 1, //
        pageSize: 10, //
        total: 0 //
      },
      load: false,
      multipleSelection: [],
      columbList: [
        {
          prop: "userId",
          label: "id",
          width: 200
        },
        {
          prop: "nickName",
          label: ""
        },
        {
          prop: "title",
          label: ""
        },
        {
          prop: "content",
          label: ""
        },
        {
          prop: "	createBy",
          label: ""
        },
        {
          prop: "createTime",
          label: ""
        },
        {
          prop: "delFlag",
          label: " "
        }
      ],
      tableData: [
        // {
        //   viewId: "001",
        //   account: "",
        //   title: "",
        //   content: "",
        //   gmt_create: "2020-4-14",
        //   is_deleted: ""
        // },
        // {
        //   viewId: "001",
        //   account: "",
        //   title: "",
        //   content: "",
        //   gmt_create: "2020-4-14",
        //   is_deleted: ""
        // },
        // {
        //   viewId: "001",
        //   account: "",
        //   title: "",
        //   content: "",
        //   gmt_create: "2020-4-14",
        //   is_deleted: ""
        // },
        // {
        //   viewId: "001",
        //   account: "",
        //   title: "",
        //   content: "",
        //   gmt_create: "2020-4-14",
        //   is_deleted: ""
        // },
        // {
        //   viewId: "001",
        //   account: "",
        //   title: "",
        //   content: "",
        //   gmt_create: "2020-4-14",
        //   is_deleted: ""
        // }
      ]
    };
  },
  mounted() {
    this.getData();
  },
  methods: {
    // handleClick(row) {
    //   alert(row);
    // },
    // 
    search() {
      this.getData();
    },
    //
    getData() {
      this.$axios
        .get(
          "prod-api/backend/advice/list?" +
            "pageNum=" +
            this.page.pageNum +
            "&pageSize=" +
            this.page.pageSize +
            "&searchValue=" +
            this.view.queryValue
        )
        .then(res => {
          if (200 == res.data.code) {
            this.tableData = res.data.rows;
            this.page.total = res.data.total;
            this.$message({
              showClose: true,
              message: "",
              type: "success"
            });
          } else {
            this.$message({
              showClose: true,
              message: res.data.msg,
              type: "error"
            });
          }
        });
    },
    // 
    // resetfields
    // $ref
    resetForm(formName) {
      if (this.$refs[formName] !== undefined) {
        this.$refs[formName].resetFields();
      }
    },

    handleSelectionChange(row) {
      this.multipleSelection = row;
      //JSON.stringify
      // alert(JSON.stringify(this.multipleSelection));
    },
    // 
    handleSizeChange(val) {
      this.page.pageSize = val;
      this.getData();
    },
    // 
    handleCurrentChange(val) {
      this.page.pageNum = val;
      this.getData();
    }
  }
};
</script>

<style lang="scss" scoped>
.formData {
  background: #fff;
}
.clickBtn {
  margin-top: 10px;
  padding: 5px;
}

.contentB {
  height: 100%;
  background: #fff;

  margin: 10px;
}
</style>



