<template>
  <div class="user">
    <!--  -->
    <div class="FormData">
      <el-card class="searchModule">
        <el-form :model="user" label-width="100px" :inline="true" :ref="user">
          <el-form-item label prop="queryValue">
            <el-input v-model="user.queryValue" clearable></el-input>
          </el-form-item>

          <el-button type="primary" @click="search()"></el-button>
          <el-button type="primary" @click="resetForm(user)"></el-button>
        </el-form>
      </el-card>
    </div>

    <div class="contentB">
      <el-card>
        <div class="clickBtn">
          <el-button class="operateBtn" type="primary" @click="addData"></el-button>
          <el-button type="primary" @click="editData"></el-button>
          <el-button type="primary" @click="isOpen()"></el-button>
          <el-button type="primary" @click="isStop()"></el-button>
          <el-button type="primary" @click="delData()"></el-button>
          <el-button type="primary" @click="isReset()"></el-button>
        </div>

        <div class="tableData">
          <sirui-table
            class="operateTable"
            :page="page"
            :loading="load"
            :data="tableData"
            @handleSelectionChange="handleSelectionChange"
            @handleSizeChange="handleSizeChange"
            @handleCurrentChange="handleCurrentChange"
            :tableHeight="tableHeight"
          >
            <el-table-column align="center" type="selection" width="45"></el-table-column>
            <el-table-column
              v-for="(item,index) in columbList"
              :key="index"
              :prop="item.prop"
              :label="item.label"
              :width="item.width"
              :sortable="item.sort"
              :fixed="item.fixed"
              align="center"
              show-overflow-tooltip
            ></el-table-column>
          </sirui-table>
        </div>
      </el-card>
    </div>
    <UserAdd ref="addData" v-on:search="search"></UserAdd>
    <UserEdit ref="editData"></UserEdit>
  </div>
</template>

<script>
import SiruiTable from "@/components/SiruiTable";
import UserAdd from "@/userManages/UserAdd";
import UserEdit from "@/userManages/UserEdit";

export default {
  name: "UserManages",
  components: {
    SiruiTable,
    UserAdd,
    UserEdit
  },
  data() {
    return {
      user: {
        queryValue: ""
      },
      tableHeight: 400,
      dialogVisible: false,
      page: {
        pages: 0, //
        pageNum: 1, //
        pageSize: 10, //
        total: 0 //
      },
      load: false,
      multipleSelection: [],
      rules: {},
      columbList: [
        {
          prop: "userName",
          label: "",
          width: 200
        },
        {
          prop: "nickName",
          label: ""
        },
        {
          prop: "phonenumber",
          label: ""
        },
        {
          prop: "email",
          label: ""
        },
        {
          prop: "status",
          label: ""
        }
      ],

      tableData: [
        // {
        //   userName: "user1",
        //   nickName: "",
        //   phone: "888888",
        //   idCard: "2101241999454545656",
        //   isEnable: ""
        // },
        // {
        //   userName: "user2",
        //   nickName: "",
        //   phone: "888888",
        //   idCard: "2101241999454545656",
        //   isEnable: ""
        // },
        // {
        //   userName: "user3",
        //   nickName: "",
        //   phone: "888888",
        //   idCard: "2101241999454545656",
        //   isEnable: ""
        // },
        // {
        //   userName: "user4",
        //   nickName: "",
        //   phone: "888888",
        //   idCard: "2101241999454545656",
        //   isEnable: ""
        // },
        // {
        //   userName: "user5",
        //   nickName: "",
        //   phone: "888888",
        //   idCard: "2101241999454545656",
        //   isEnable: ""
        // }
      ]
    };
  },
  mounted() {
    this.getData();
  },
  methods: {
    // handleClick(row) {
    //   alert(row);
    // },
    // 
    search() {
      this.getData();
    },
    //
    getData() {
      this.$axios
        .get(
          "prod-api/backend/user/list?" +
            "pageNum=" +
            this.page.pageNum +
            "&pageSize=" +
            this.page.pageSize +
            "&searchValue=" +
            this.user.queryValue
        )
        .then(res => {
          if (200 == res.data.code) {
            this.tableData = res.data.rows;
            this.page.total = res.data.total;
            this.$message({
              showClose: true,
              message: "",
              type: "success"
            });
          } else {
            this.$message({
              showClose: true,
              message: res.data.msg,
              type: "error"
            });
          }
        });
    },
    // 
    // resetfields
    // $ref
    resetForm(formName) {
      if (this.$refs[formName] !== undefined) {
        this.$refs[formName].resetFields();
      }
    },

    handleSelectionChange(row) {
      this.multipleSelection = row;
      //JSON.stringify
      // alert(JSON.stringify(this.multipleSelection));
    },
    // 
    handleSizeChange(val) {
      this.page.pageSize = val;
      this.getData();
    },
    // 
    handleCurrentChange(val) {
      this.page.pageNum = val;
      this.getData();
    },

    // 
    addData() {
      this.$refs.addData.openDialog(true);
    },
    //
    editData() {
      if (1 == this.multipleSelection.length) {
        //
        this.$refs.editData.openDialog(
          true,

          JSON.parse(JSON.stringify(this.multipleSelection[0]))
        );
      } else {
        this.$message({
          showClose: true,
          message: "",
          type: "error"
        });
      }
    },
    //
    isOpen() {
      if (this.multipleSelection.length >= 1) {
        if (0 != this.multipleSelection.length) {
          this.$confirm("", "", {
            confirmButtonText: "",
            cancelButtonText: "",
            type: "warning"
          })
            .then(() => {
              let param = {
                userIds: []
              };
              for (var i = 0; i < this.multipleSelection.length; i++) {
                param.userIds.push(this.multipleSelection[i].userId);
              }
              param.userIds = param.userIds.join(",");
              this.$axios
                .put("prod-api/backend/user/enable/" + param.userIds, param)
                .then(res => {
                  if (200 == res.data.code) {
                    this.$message({
                      showClose: true,
                      message: res.data.msg,
                      type: "success"
                    });
                  } else {
                    this.$message({
                      showClose: true,
                      message: res.data.msg,
                      type: "error"
                    });
                  }
                });
            })
            .catch(() => {
              this.$message({
                type: "info",
                message: ""
              });
            });
        }
      } else {
        this.$message({
          showClose: true,
          message: "",
          type: "error"
        });
      }
    },

    //
    isStop() {
      if (this.multipleSelection.length >= 1) {
        if (0 != this.multipleSelection.length) {
          this.$confirm("", "", {
            confirmButtonText: "",
            cancelButtonText: "",
            type: "warning"
          })
            .then(() => {
              let param = {
                userIds: []
              };
              for (var i = 0; i < this.multipleSelection.length; i++) {
                param.userIds.push(this.multipleSelection[i].userId);
              }
              param.userIds = param.userIds.join(",");
              this.$axios
                .put("prod-api/backend/user/disable/" + param.userIds, param)
                .then(res => {
                  if (200 == res.data.code) {
                    this.$message({
                      showClose: true,
                      message: res.data.msg,
                      type: "success"
                    });
                  } else {
                    this.$message({
                      showClose: true,
                      message: res.data.msg,
                      type: "error"
                    });
                  }
                });
            })
            .catch(() => {
              this.$message({
                type: "info",
                message: ""
              });
            });
        }
      } else {
        this.$message({
          showClose: true,
          message: "",
          type: "error"
        });
      }
    },

    //
    isReset() {
      if (this.multipleSelection.length >= 1) {
        this.dialogPwdEdit = true;
        let param = {
          userIds: []
        };
        for (var i = 0; i < this.multipleSelection.length; i++) {
          param.userIds.push(this.multipleSelection[i].userId);
        }
        param.userIds = param.userIds.join(",");
        this.$axios
          .put("prod-api/backend/user/reset/" + param.userIds, param)
          .then(res => {
            if (200 == res.data.code) {
              this.$message({
                showClose: true,
                message: res.data.msg,
                type: "success"
              });
            } else {
              this.$message({
                showClose: true,
                message: res.data.msg,
                type: "error"
              });
            }
          });
      }
    },

    //
    delData() {
      if (this.multipleSelection.length >= 1) {
        if (this.multipleSelection.length != 0) {
          this.$confirm(",?", "", {
            confirmButtonText: "",
            cancelButtonText: "",
            type: "warning"
          })
            .then(() => {
              //
              let params = {
                userIds: []
              };
              for (var i = 0; i < this.multipleSelection.length; i++) {
                params.userIds.push(this.multipleSelection[i].userId);
              }
              params.userIds = params.userIds.join(",");
              this.$axios
                .delete("prod-api/backend/user/delete/" + params.userIds)
                .then(res => {
                  // this.loading.endLoading();
                  if (200 == res.data.code) {
                    // this.searchForm();
                    this.$message({
                      type: "success",
                      message: res.data.msg
                    });
                  } else {
                    this.$message({
                      showClose: true,
                      message: "",
                      type: "error"
                    });
                  }
                });
            })
            .catch();
        }
      }
    }
  }
};
</script>

<style lang="scss" scoped>
.formData {
  background: #fff;
}
.clickBtn {
  margin-top: 10px;
  padding: 5px;
}

.contentB {
  height: 100%;
  background: #fff;

  margin: 10px;
}
</style>



