<template>
  <div class="order">
    <div class="formData">
      <el-card class="searchModule">
        <el-form :model="order" label-width="100px" :inline="true" :ref="order">
          <el-form-item label="" prop="nickName">
            <el-input v-model="order.nickName" clearable></el-input>
          </el-form-item>
          <el-form-item label="" prop="status">
            <el-select v-model="order.status" placeholder="">
              <el-option
                v-for="item in options"
                :key="item.value"
                :label="item.label"
                :value="item.value"
              ></el-option>
            </el-select>
          </el-form-item>
          <br />
          <el-form-item label="" prop="time">
            <el-date-picker
              v-model="order.time"
              type="daterange"
              start-placeholder=""
              end-placeholder=""
              :default-time="['00:00:00', '23:59:59']"
            ></el-date-picker>
          </el-form-item>

          <el-button type="primary" @click="search()"></el-button>
          <el-button type="primary" @click="resetForm(order)"></el-button>
        </el-form>
      </el-card>
    </div>
    <div class="table">
      <el-card>
        <div class="btn">
          <el-button type="primary" @click="detail()"></el-button>
          <el-button type="primary" @click="delData"></el-button>
        </div>

        <div class="tabledata">
          <sirui-table
            class="operateTaable"
            :page="page"
            :loading="load"
            :data="tableData"
            @handleSelectionChange="handleSelectionChange"
            @handleSizeChange="handleSizeChange"
            @handleCurrentChange="handleCurrentChange"
            :tableHeight="tableHeight"
          >
            <el-table-column align="center" type="selection" width="45"></el-table-column>

            <el-table-column prop="orderName" label="" show-overflow-tooltip></el-table-column>
            <el-table-column prop="orderCode" label="" show-overflow-tooltip></el-table-column>
            <el-table-column prop="status" label="" show-overflow-tooltip></el-table-column>
            <el-table-column prop="createBy" label="" show-overflow-tooltip></el-table-column>
            <el-table-column prop="boxId" label="id" show-overflow-tooltip></el-table-column>
            <el-table-column prop="boxCode" label="" :show-overflow-tooltip="true"></el-table-column>
            <el-table-column prop="boxStandard" label="" :show-overflow-tooltip="true"></el-table-column>
            <el-table-column prop="leaseDuration" label="" show-overflow-tooltip></el-table-column>

            <el-table-column prop="totalPrice" label="" show-overflow-tooltip></el-table-column>
            <el-table-column prop="terminal" label="" fixed="right" show-overflow-tooltip>
              <template slot-scope="scope">
                <el-button
                  size="mini"
                  @click="sendNull(scope.row)"
                  v-if="scope.row.btn===''"
                  style="color:orange;"
                ></el-button>
                <el-button size="mini" v-else-if="scope.row.btn===''" style="color:green"></el-button>
                <el-button size="mini" v-else-if="scope.row.btn===''" style="color:blue"></el-button>
                <el-button size="mini" v-else-if="scope.row.btn===''" style="color:pink"></el-button>
                <el-button size="mini" v-else style="display:none">--</el-button>
              </template>
            </el-table-column>
            <el-table-column
              prop="emptyBoxOrderTime"
              label=""
              v-if="isShow"
              show-overflow-tooltip
            ></el-table-column>
            <el-table-column
              prop="heavyBoxOrderTime"
              label=""
              v-if="isShow2"
              show-overflow-tooltip
            ></el-table-column>
          </sirui-table>
        </div>
      </el-card>
    </div>
    <OrderDetail ref="detail"></OrderDetail>
  </div>
</template>

<script>
import SiruiTable from "@/components/SiruiTable";
import OrderDetail from "@/orderManages/OrderDetail";
export default {
  name: "OrderManages",
  components: {
    SiruiTable,
    OrderDetail
  },
  mounted: function() {
    this.getData();
  },
  data() {
    return {
      isShow: false,

      tableHeight: 400,
      isShow2: false,
      order: {
        id: "",
        nickName: "",
        status: "",
        emptyBoxOrderTimeStart: "",
        emptyBoxOrderTimeEnd: "",
        heavyBoxOrderTimeStart: "",
        heavyBoxOrderTimeEnd: ""
      },

      dialogVisible: false,
      page: {
        pages: 0, //
        pageNum: 1, //
        pageSize: 10, //
        total: 0 // 
      },
      load: false,
      multipleSelection: [],
      options: [
        {
          value: "1",
          label: ""
        },
        {
          value: "2",
          label: ""
        },
        {
          value: "3",
          label: ""
        },
        {
          value: "4",
          label: ""
        },
        {
          value: "5",
          label: ""
        },
        {
          value: "6",
          label: ""
        },
        {
          value: "7",
          label: ""
        },
        {
          value: "8",
          label: ""
        },
        {
          value: "9",
          label: ""
        },
        {
          value: "10",
          label: ""
        }
      ],
      columnList: [],
      tableData: []
    };
  },
  methods: {
    //
    search() {
      this.getData();
    },
    sendNull(row) {
      let operate = 0;

      if (row.status == 1) {
        operate = 2;
      } else if (row.status == 2) {
        operate = 3;
      } else if (row.status == 3) {
        operate = 4;
      } else if (row.status == 4) {
        operate = 5;
      } else if (row.status == 5) {
        operate = 6;
      } else if (row.status == 6) {
        operate = 7;
      } else if (row.status == 7) {
        operate = 8;
      } else if (row.status == 8) {
        operate = 9;
      } else if (row.status == 9) {
        operate = 10;
      }

      let paramForm = {};
      this.$axios
        .put(
          "prod-api/backend/order/operate/" +
            row.id +
            "/" +
            operate +
            "/" +
            row.version,
          paramForm
        )
        .then(res => {
          if (200 == res.data.code) {
            this.$message({
              showClose: true,
              message: res.data.msg,
              type: "success"
            });
          } else {
            this.$message({
              showClose: true,
              message: res.data.msg,
              type: "error"
            });
          }
        });
    },
    //
    getData() {
      this.$axios
        .get(
          "prod-api/backend/order/list?" +
            "pageNum=" +
            this.page.pageNum +
            "&pageSize=" +
            this.page.pageSize +
            "&nickName=" +
            this.order.nickName +
            "&status=" +
            this.order.status +
            "&emptyBoxOrderTimeStart=" +
            this.order.emptyBoxOrderTimeStart +
            "&emptyBoxOrderTimeEnd=" +
            this.order.emptyBoxOrderTimeEnd +
            "&heavyBoxOrderTimeStart=" +
            this.order.heavyBoxOrderTimeStart +
            "&heavyBoxOrderTimeEnd=" +
            this.order.heavyBoxOrderTimeEnd
        )

        .then(res => {
          if (200 == res.data.code) {
            this.tableData = res.data.rows;
            this.page.total = res.data.total;
            for (var i = 0; i < this.tableData.length; i++) {
              if (this.tableData[i].status == "1") {
                this.tableData[i].btn = "";
              } else if (this.tableData[i].status == "4") {
                this.tableData[i].btn = "";
              } else if (this.tableData[i].status == "6") {
                this.tableData[i].btn = "";
              } else if (this.tableData[i].status == "9") {
                this.tableData[i].btn = "";
              }
            }
            this.$message({
              showClose: true,
              message: "",
              type: "success"
            });
          } else {
            this.$message({
              showClose: true,
              message: "res.data.msg",
              type: "error"
            });
          }
        });
    },
    // 
    // resetfields
    // $ref
    resetForm(formName) {
      if (this.$refs[formName] !== undefined) {
        this.$refs[formName].resetFields();
      }
    },

    handleSelectionChange(row) {
      this.multipleSelection = row;
      //JSON.stringify
      // alert(JSON.stringify(this.multipleSelection));
    },
    // 
    handleSizeChange(val) {
      this.page.pageSize = val;
      this.getData();
    },
    // 
    handleCurrentChange(val) {
      this.page.pageNum = val;
      this.getData();
    },

    //
    detail() {
      if (1 == this.multipleSelection.length) {
        this.$refs.detail.openDialog(
          true,
          JSON.parse(JSON.stringify(this.multipleSelection[0]))
        );
      } else {
        this.$message({
          showClose: true,
          message: "",
          type: "error"
        });
      }
    },

    //
    delData() {
      if (this.multipleSelection.length >= 1) {
        if (this.multipleSelection.length != 0) {
          this.$confirm(",?", "", {
            confirmButtonText: "",
            cancelButtonText: "",
            type: "warning"
          })
            .then(() => {
              //
              let params = {
                ids: []
              };
              for (var i = 0; i < this.multipleSelection.length; i++) {
                params.ids.push(this.multipleSelection[i].id);
              }
              params.ids = params.Ids.join(",");
              this.$axios
                .delete("prod-api/backend/order/delete/" + params.ids)
                .then(res => {
                  // this.loading.endLoading();
                  if (200 == res.data.code) {
                    // this.searchForm();
                    this.$message({
                      type: "success",
                      message: res.data.msg
                    });
                  } else {
                    this.$message({
                      showClose: true,
                      message: "",
                      type: "error"
                    });
                  }
                });
            })
            .catch();
        }
      }
    }
  }
};
</script>
<style lang="scss" scoped>
.user {
  position: relative;
}
.btnmm {
  position: absolute;
  right: 675px;
  top: 85px;
}
.btnnn {
  position: absolute;
  right: 575px;
  top: 85px;
}
.table {
  margin-top: 11px;
}
</style>



